import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import second from '@/components/second'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/second',
      name: 'second',
      component: second
    }
  ]
})



