<template>
  <canvas v-on:click="mouseclick" v-on:mousemove="updateCoordinates" id="canvas" width="500" height="500" />
</template>

<script>
export default {
  name: 'index',
  data () {
    return {
      x1: 0,
      y1: 0,
      x2: 0,
      y2: 0,
      isActive: false
    }
  },
  methods: {
    draw (x1, y1, x2, y2, isActive) {
      if (isActive === false) return console.log('not clicked')
      this.ctx.beginPath()
      this.ctx.lineCap = 'round'
      this.ctx.strokeStyle = 'black'
      this.ctx.lineWidth = 10
      this.ctx.moveTo(x1, y1)
      this.ctx.lineTo(x2, y2)
      this.ctx.stroke()
      this.ctx.closePath()
    },
    mouseclick: event => {
      this.x1 = event.clientX
      this.y1 = event.clientY
      this.isActive = true
      console.log(this.x1, this.y1)
      this.draw(this.x1, this.y1, this.x2, this.y2, this.isActive)
    },
    updateCoordinates: event => {
      this.x2 = event.clientX
      this.y2 = event.clientY
    }
  },
  mounted () {
    this.ctx = this.$el.getContext('2d')
    this.draw(this.x1, this.y1, this.x2, this.y2, this.isActive)
  }
}
</script>

<style scoped>
  * { padding: 0; margin: 0; }
  canvas { background: #eee; display: block; margin: 0 auto; position: absolute;}
  #ui-layer { z-index: 3; }
  #canvas { z-index: 2; }
  #background-layer { z-index: 1; }
</style>



